import mInput from '@/components/m-input.vue';

export default {
	components: {
		mInput
	},
	data() {
		return {
			providerList: [],
			hasProvider: false,
			tel: '',
			code: '',
			positionTop: 0,
			globalData: {},
			userInfo: {},
			show: true,
			imei: '', //设备唯一标识
			timestamp: '',
			count: 0
		};
	},
	onLoad() {
		let _this = this;
		this.timestamp = new Date().getTime();

		uni.login({
			provider: 'weixin',
			success(loginRes) {
				console.log(JSON.stringify(loginRes));
				// 获取用户信息
				// uni.getUserInfo({
				//   provider: 'weixin',
				//   success (infoRes) {
				//     console.log('用户昵称为：' + JSON.stringify(infoRes));
				//   }
				// });
			}
		});








	},
	methods: {
		getPhoneNumber(e) {
			console.log(e.detail.errMsg);
			console.log(e.detail.iv);
			console.log(e.detail.encryptedData);
		},

		getCode() {
			if (!this.$uitls.isPhone(this.tel)) {
				return;
			}
			let data = {
				tel: this.tel
			};
			uni.showLoading({
				title: '正在获取验证码'
			});
			this.timers();
			// this.$api.sendSms(data, res => {
			// 	uni.showToast({
			// 		icon: 'none',
			// 		title: res.msg
			// 	});
			// 	this.timers();
			// });
		},
		//倒计时
		timers() {
			const TIME_COUNT = 120;
			if (!this.timer) {
				this.count = TIME_COUNT;
				this.show = false;
				this.timer = setInterval(() => {
					if (this.count > 0 && this.count <= TIME_COUNT) {
						this.count--;
					} else {
						this.show = true;
						clearInterval(this.timer);
						this.timer = null;
					}
				}, 1000);
			}
		},
		initProvider() {
			const filters = ['weixin'];
			uni.getProvider({
				service: 'oauth',
				success: res => {
					if (res.provider && res.provider.length) {
						for (let i = 0; i < res.provider.length; i++) {
							if (~filters.indexOf(res.provider[i])) {
								if (res.provider[i] === 'weixin') {
									this.providerList.push({
										value: res.provider[i],
										image: '../../static/img/' + res.provider[i] + '.png'
									});
								}
							}
						}
						this.hasProvider = true;
					}
				},
				fail: err => {
					console.error('获取服务供应商失败：' + JSON.stringify(err));
				}
			});
		},

		initPosition() {
			/**
			 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			 * 反向使用 top 进行定位，可以避免此问题。
			 */
			this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
		},
		bindLogin() {
			let data = {
				timestamp: new Date().getTime(),
				appkey: this.$store.state.appkey,
				tel: this.tel,
				imei: this.imei,
				code: this.code
			};
			if (!this.$uitls.isPhone(this.tel)) {
				return;
			}
			if (!this.code) {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码'
				});
				return;
			}

			let datas = {
				login_token: '12213212123'
			};

			try {
				uni.setStorageSync('loginToken', datas);
				this.$store.commit('loginToken', datas);
			} catch (e) {
				//TODO handle the exception
			}

			if (this.$store.state.hasLogin) {
				this.$Router.replaceAll({ name: 'index' });
			}

			// this.$api.logins(data, res => {
			// 	try {
			// 		uni.setStorageSync('loginToken', res.data);
			// 		this.$store.commit('loginToken', res.data);
			// 	} catch (e) {
			// 		//TODO handle the exception
			// 	}

			// 	if (this.$store.state.hasLogin) {
			// 		this.$Router.replaceAll({ name: 'index' });
			// 	}
			// });
		},
		oauth(value) {
			// uni.showLoading({
			// 	title:'正在登陆...'
			// })
			uni.login({
				provider: value,
				success: res => {
					uni.getUserInfo({
						provider: value,
						success: infoRes => {
							/**
							 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
							 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
							 */
							this.toMain(infoRes.userInfo);
						}
					});
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '授权登录失败：' + JSON.stringify(err.errMsg)
					});
				}
			});
		},
		toMain(userInfo) {
			this.$store.commit('loginWeChat', userInfo);
			if (this.$store.state.hasAuthorized) {
				this.login();
			} else {
				uni.navigateBack();
			}
		},
		login() {
			let data = {
				openid: this.$store.state.wxUserInfo.openId,
				appkey: this.$store.state.appkey,
				timestamp: this.timestamp,
				tel: this.tel,
				imei: this.imei
			};
			if (!this.tel) {
				uni.showToast({
					icon: 'none',
					title: '绑定手机号'
				});
				return;
			}
			if (!this.$uitls.isPhone(this.tel)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号'
				});
				return;
			}
			this.$api.logins(data, res => {
				this.$store.commit('loginToken', res.data);
				if (this.$store.state.hasLogin) {
					this.$Router.replaceAll({ name: 'main' });
				}
			});
		}
	},
	onReady() {




	}
};
